body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



* {
    margin: 0;
    padding: 0;
}

nav {
    background-color: rgba(5, 5, 5, 0.911);
    display: flex;
    justify-content: space-between;
    padding: 10px 40px;
}

nav h1 {
    color: white;
}

nav ul {
    color: white;
    display: flex;
    justify-content: space-around;
    font-weight: 600;
    font-size: large;
}

nav ul li {
    list-style: none;
    padding: 2px 25px;
}

nav li a {
    color: white;
    text-decoration: none;
    font-weight: 600;
    font-size: large;

}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: rgb(247, 241, 245);
  text-transform: full-width;
}

p {
  color: white;
  margin: 80px;
  font-size: 20px;
  padding: 5px;
  text-indent: 60px;
  text-align: justify;
}

.homeTitle {
  font-size: 48px;
  padding: 15px;
  padding-top: 30px;
  padding-bottom: 60px;
  font-weight: 800;
  color: rgb(155, 108, 173);
  font-family: "Bellota", cursive;
  text-align: center;
  text-decoration: underline;
  text-shadow: 1px 1px 1px rgb(66, 66, 66), 3px 3px 8px rgb(139, 137, 137);
}

.mainMargin {
  display: flex;
  justify-content: center;
}

.maintextalign {
  text-align: center justify;
}

.backcolor {
  background-color: rgb(231, 228, 231);
}

.container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

.container img {
  vertical-align: middle;
}

.txtindent {
  text-indent: 60px;
  text-align: justify;
  line-height: 1.5;
  text-shadow: 1px 1px 1px rgb(66, 66, 66), 3px 3px 8px black;
  font-size: 18px;
}

